import React from 'react';
import './ImageLinkForm.css';
const ImageLinkFrom =({onInputChange,onSubmit})=>{
    return (
    <div>
        <p className='f3'>
              {'This is a magic Brain will detect faces in you pics.let try'}
        </p>
        <div className='center'>
        <div className ='form center pa4 br3 shadow-5'>
              <input className='center f4 pa2 w-70 center' type="tex" onChange={onInputChange} ></input>
              <button className='center w-30 grow f4 link ph3 pv2 dib white bg-light-purple' onClick={onSubmit}>Detect</button>
        </div></div>
    </div>
    )
}
export default ImageLinkFrom;