import React from 'react';
import '../Logo/Logo.css';
const Navigation=({onRouteChange,route})=>{
    return (<div>
    {(() => {
        switch(route) {
          case 'home':
            return (
        <nav style={{display:'flex',justifyContent: 'flex-end'}}>
        <button className="f6 link dim br3 ba pa2 bw2 ph3 pv2 mb2 dib bg-transparent "> <p className='f3 link dim black pointer dib' onClick={()=>onRouteChange('signin')}>Sign out</p></button>
        </nav>
    ) ;
          case 'signup':
            return (
        <nav style={{display:'flex',justifyContent: 'flex-end'}}>
        <button className=" f6 link dim br3 ba pa2 bw2 ph3 pv2 mb2 dib bg-transparent"><p className='f3 link dim black pointer dib' onClick={()=>onRouteChange('signin')}>Sign in</p></button>
        </nav>
    ) ;
          default:return (
        <nav style={{display:'flex',justifyContent: 'flex-end'}}>
        <button className=" f6 link dim br3 ba pa2 bw2 ph3 pv2 mb2 dib bg-transparent"><p className='f3 link dim black pointer dib' onClick={()=>onRouteChange('signup')}>Sign up</p></button>
        </nav>
    ) ;
        
        }
      })()}
      </div>)
    
}
export default Navigation;